.rations {
  padding: 60px 0 120px;
  overflow: hidden;
  &__title {
    font-family: 'Tahoma';
    font-size: 56px;
    line-height: 120%;
    color: #2F2F2F;
  }
  &__text {
    font-size: 20px;
    line-height: 150%;
    color: #2F2F2F;
    margin-top: 30px;
  }
  &__sorting {
    max-width: 61px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: #ffffff;
    border-radius: 3px;
    margin: 0 0 0 auto;
  }
  &__sorting-btn {
    width: 50%;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &--active {
      background: #F15524;
      svg rect {
        fill: #ffffff;
      }
    }
    &:first-child {
      border-radius: 3px 0 0 3px;
    }
    &:last-child {
      border-radius: 0 3px 3px 0;
    }
    svg {
      width: 19px;
      height: auto;
    }
  }
  &-filter__mob-btns {
    display: none;
    justify-content: flex-end;
    align-items: center;
    margin-top: 30px;
  }
  &-filter__mob-btn {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #A1AEBA;
    border-radius: 10px;
    cursor: pointer;
    svg {
      width: 16px;
      height: auto;
    }
  }
  &-filter__mob-btn2 {
    width: 300px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #A1AEBA;
    border-radius: 10px;
    margin-left: 10px;
    cursor: pointer;
    i {
      width: 18px;
      margin-left: 12px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 12px;
      line-height: 120%;
      color: #4F5B66;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  &-filter {
    width: 365px;
    margin-right: 30px;
  }
  &__content {
    width: calc(100% - 395px);
  }
  &__cards {
    display: none;
    grid-template-columns: 100%;
    gap: 20px;
    align-items: stretch;
    &--active {
      display: grid;
    }
  }
  &__btn {
    width: 310px;
    height: 70px;
    background: #F15524;
    border-radius: 15px;
    margin: 60px auto 0;
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
    }
    &:hover {
      background: #fa440c;
    }
  }
}
.rations-filter {
  padding-bottom: 30px;
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__top {
    display: none;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
  }
  &__group {
    .rations-filter__apply {
      display: none;
    }
  }
  &__group-mob {
    display: none;
    border-bottom: 1px solid rgba(161,174,186,.5);
    padding-bottom: 20px;
  }
  &__close {
    width: 25px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__mob-title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #2F2F2F;
  }
  &__clear {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #F15524;
    cursor: pointer;
  }
  &__group-top {
    display: none;
    justify-content: space-between;
    align-items: center;
  }
  &__close2 {
    width: 25px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 20px;
  }
  &__btn-mob {
    height: 30px;
    background: #F15524;
    border-radius: 34px;
    padding: 3px 15px;
    margin: 0 10px 10px 0;
    i {
      width: 8px;
      margin-left: 8px;
      img {
        width: 100%;
      }
    }
    span {
      ont-size: 12px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
    }
    &:hover {
      background: #fa440c;
    }
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #2F2F2F;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 5px;
  }
  &__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(161, 174, 186, 0.5);
    }
    &-left {
      display: flex;
      align-items: center;
    }
    &-right {
      margin-left: 15px;
    }
    &-img {
      flex-shrink: 0;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      box-shadow: 0px 0px 40px 0px rgba(68, 68, 68, 0.06);
      border-radius: 8px;
      img {
        max-width: 38px;
      }
    }
    &-desc {
      margin-left: 14px;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      color: #2F2F2F;
    }
    &-info {
      font-size: 12px;
      line-height: 120%;
      color: #F15524;
      margin-top: 5px;
    }
    &-check {
      input {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          border-color: #F15524;
          &:after {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        position: relative;
        display: block;
        width: 20px;
        height: 20px;
        border: 1px solid #A1AEBA;
        border-radius: 4px;
        transition: all 0.3s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 12px;
          height: 8px;
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    .choosing-city {
      input {
        &:checked ~ label {
          border-color: #F15524;
          &:after {
            opacity: 1;
            pointer-events: all;
          }
        }
      }
      label {
        position: relative;
        display: block;
        width: 14px;
        height: 14px;
        border: 1px solid #A1AEBA;
        border-radius: 50%;
        transition: all 0.3s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 9px;
          height: 9px;
          background: #F15524;
          border-radius: 50%;
          opacity: 0;
          pointer-events: none;
          transition: all 0.3s;
        }
      }
    }
    &-check2 {
      width: 56px;
      height: 32px;
      input {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          background: #F15524;
          &:after {
            left: calc(100% - 28px);
            background: #ffffff;
          }
        }
      }
      label {
        position: relative;
        display: block;
        width: 56px;
        height: 32px;
        background: rgba(241, 85, 36, 0.10);
        border-radius: 16px;
        transition: all 0.3s;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 4px;
          transform: translate(0, -50%);
          width: 24px;
          height: 24px;
          background: #F15524;
          border-radius: 50%;
          transition: all 0.5s;
        }
      }
    }
  }
  .show {
    cursor: pointer;
    span:nth-child(1) {
      display: none;
    }
  }
  .show--active {
    span:nth-child(1) {
      display: block;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  &__btn {
    font-size: 16px;
    line-height: 120%;
    color: #F15524;
    cursor: pointer;
    margin-top: 5px;
  }
  &__flex {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 20px;
  }
  &__check {
    position: relative;
    width: 80px;
    height: 30px;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 10px;
    }
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        color: #ffffff;
        background: #F15524;
      }
    }
    label {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      line-height: 120%;
      font-weight: 500;
      color: #F15524;
      background: rgba(241, 85, 36, 0.10);
      border-radius: 34px;
      padding: 3px;
    }
  }
  &__range {
    margin-top: 40px;
    ._range-block {
      width: 98%;
    }
    &-numbers {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px;
    }
    &-number {
      font-size: 14px;
      line-height: 120%;
      color: #2E2E3A;
    }
    .noUi-horizontal {
      height: 6px;
      border: none;
      border-radius: 6px;
    }
    .noUi-connects {
      background: #E1E1F0;
    }
    .noUi-connect {
      background: #F15524;
    }
    .noUi-horizontal .noUi-handle {
      top: -10px;
      width: 24px;
      height: 24px;
      background: #ffffff;
      border: none;
      box-shadow: 4px 4px 8px 3px rgba(34, 60, 80, 0.2);
      border-radius: 50%;
      &:after,
      &:before {
        display: none;
      }
    }
    .noUi-tooltip {
      font-size: 14px;
      line-height: 120%;
      color: #F15524;
      background: transparent;
      border: none;
      padding: 0;
    }
  }
  &__apply {
    width: 100%;
    height: 48px;
    background: #F15524;
    border-radius: 15px;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: #fa440c;
    }
  }
}
.rations__cards2 {
  grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
  gap: 15px;
  .ration-card {
    flex-direction: column;
    border-radius: 20px;
    &__left {
      width: 100%;
    }
    &__right {
      flex-grow: 1;
      width: 100%;
      padding: 18px 12px;
    }
    &__img {
      border-radius: 10px 10px 0 0;
      padding-bottom: 52%;
      img {
        border-radius: 10px 10px 0 0;
      }
    }
    &__kcal {
      span {
        font-size: 14px;
      }
    }
    &__name {
      font-size: 16px;
    }
    &__increase {
      width: 28px;
    }
    &__title {
      font-size: 16px;
    }
    &__discount {
      span {
        font-size: 14.5px;
      }
    }
    &__item {
      width: 25px;
      height: 25px;
    }
    &__btn {
      width: 100%;
      height: 44px;
    }
  }
}
.ration-card {
  display: flex;
  align-items: stretch;
  background: #FFF;
  box-shadow: 0px 4px 35px 0px rgba(68, 68, 68, 0.04);
  border-radius: 20px;
  &__left {
    width: 355px;
    position: relative;
  }
  &__right {
    width: calc(100% - 355px);
    display: flex;
    flex-direction: column;
    padding: 40px;
  }
  &__img {
    position: relative;
    height: 100%;
    display: block;
    border-radius: 20px 0 0 20px;
    padding-bottom: 100%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 3.18%, rgba(0, 0, 0, 0.80) 100%);
      border-radius: 20px 0 0 20px;
      pointer-events: none;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px 0 0 20px;
    }
  }
  &__info-top {
    position: absolute;
    z-index: 1;
    top: 20px;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
  }
  &__info-kcal {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.30);
    backdrop-filter: blur(10px);
    border-radius: 4px;
    padding: 5px 10px;
    span {
      font-size: 16px;
      line-height: 100%;
      color: #FFF;
    }
  }
  &__favorite {
    width: 16px;
    cursor: pointer;
    margin-left: 15px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__favorite--active {
    svg path {
      fill: #F15524;
      stroke: #F15524;
    }
  }
  &__info-bottom {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 20px;
    padding: 0 20px;
  }
  &__rating {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
  }
  &__name {
    font-size: 26px;
    line-height: 100%;
    font-weight: 500;
    color: #ffffff;
  }
  &__rating-wrap {
    display: flex;
    align-items: center;
    span {
      font-size: 14px;
      line-height: 120%;
      color: #FFF;
    }
    i {
      width: 13px;
      margin-left: 6px;
      img {
        width: 100%;
      }
    }
  }
  &__feedback {
    font-size: 14px;
    line-height: 120%;
    color: #ffffff;
    opacity: 0.5;
    margin-left: 6px;
  }
  &__increase {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 26px;
    line-height: 120%;
    font-weight: 700;
    color: #2F2F2F;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 12px;
  }
  .select__wrap {
    position: relative;
    margin-right: 8px;
    margin-bottom: 10px;
  }
  .select__wrap--active {
    .select__list {
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__list-default {
    li {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
  }
  .select__current {
    height: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14.5px;
    line-height: 120%;
    color: #2F2F2F;
    border: 0.459px solid #A1AEBA;
    border-radius: 4px;
    padding: 2px 8px;
  }
  .select-arrow {
    width: 27px;
    height: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F15524;
    border-radius: 4px;
    margin-left: 4px;
  }
  .select__list {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 375px;
    background: #ffffff;
    filter: drop-shadow(0px 1px 49px rgba(0, 0, 0, 0.10));
    border-radius: 20px;
    padding: 30px 10px 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .select__title {
    text-align: center;
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #2F2F2F;
    margin-bottom: 5px;
  }
  .select__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    cursor: pointer;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(161, 174, 186, 0.5);
    }
    &:last-child {
      padding-bottom: 0;
    }
  }
  .li--active {
    .select__item-check {
      border-color: #F15524;
    }
    .select__item-check:after {
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__item-left {
    display: flex;
    align-items: center;
  }
  .select__item-img {
    flex-shrink: 0;
    width: 60px;
    img {
      width: 100%;
    }
  }
  .select__item-desc {
    margin-left: 14px;
  }
  .select__item-title {
    font-size: 16px;
    line-height: 120%;
    color: #2F2F2F;
  }
  .select__price-wrap {
    display: flex;
    align-items: center;
    margin-top: 7px;
  }
  .select__price-title {
    font-size: 12px;
    line-height: 120%;
    color: #2F2F2F;
  }
  .select__price {
    display: flex;
    align-items: center;
    margin-left: 8px;
    span:nth-child(1) {
      font-size: 12px;
      line-height: 120%;
      color: #F15524;
    }
    span:nth-child(2) {
      font-size: 12px;
      line-height: 120%;
      color: #A1AEBA;
      text-decoration: line-through;
      margin-left: 6px;
    }
  }
  .select__item-check {
    position: relative;
    display: block;
    width: 14px;
    height: 14px;
    border: 1px solid #A1AEBA;
    border-radius: 50%;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 9px;
      height: 9px;
      background: #F15524;
      border-radius: 50%;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  &__discount {
    width: 160px;
    height: 30px;
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 120%;
    color: #6AAF00;
    background: #F1F7E6;
    border-radius: 4px;
    padding: 3px 4px;
    margin-bottom: 10px;
  }
  &__code {
    position: relative;
    width: 168px;
    height: 30px;
    justify-content: flex-end;
    font-size: 15px;
    line-height: 120%;
    background: #ffffff;
    border: 0.5px solid rgba(161, 174, 186, 0.5);
    border-radius: 4px;
    padding: 3px 4px;
    margin-bottom: 10px;
    span:nth-child(1) {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 82%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #6AAF00;
    }
    span:nth-child(2) {
      color: #606060;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 82%;
      height: 100%;
      background: #F1F7E6;
      transition: all 0.3s;
    }
    &:hover {
      &:after {
        width: 78%;
      }
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 5px;
  }
  &__item {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.5px solid #A1AEBA;
    border-radius: 4px;
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 15px;
    }
    img {
      max-width: 18px;
    }
  }
  &__item-show {
    position: relative;
    &:hover {
      .ration-card__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
  &__item-hidden {
    position: absolute;
    left: -75px;
    bottom: calc(100% + 15px);
    width: 300px;
    opacity: 0;
    pointer-events: none;
    &:after {
      content: '';
      position: absolute;
      left: 67px;
      bottom: -30px;
      border: 20px solid transparent;
      border-top: 20px solid rgba(82, 86, 87, 0.70);
    }
  }
  &__item-wrap {
    position: relative;
    z-index: 1;
    background: rgba(82, 86, 87, 0.70);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 20px;
  }
  &__item-title {
    font-size: 14px;
    line-height: 120%;
    color: #ffffff;
  }
  &__item-text {
    font-size: 12px;
    line-height: 150%;
    color: #ffffff;
    margin-top: 8px;
  }
  &__text {
    font-size: 14px;
    line-height: 150%;
    color: #2F2F2F;
    margin-top: 5px;
    margin-bottom: 20px;
  }
  &__btn {
    width: 194px;
    height: 48px;
    background: #F15524;
    border-radius: 10px;
    margin: auto 0 0 auto;
    span {
      font-size: 12px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
    }
    &:hover {
      background: #fa440c;
    }
  }
}
.filter-sorting {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #F9F9F9;
  padding: 20px 15px 120px;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  &__close {
    text-align: end;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #F15524;
    cursor: pointer;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 50px;
  }
  &__item {
    border-bottom: 1px solid rgba(161,174,186,.5);
    padding: 20px 0;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: unset;
    }
  }
}
.bold-text {
  font-weight: 700;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 1300px) {
  .rations {
    &-filter {
      width: 300px;
    }
    &__content {
      width: calc(100% - 330px);
    }
    &__cards2 {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
  .ration-card {
    &__left {
      width: 300px;
    }
    &__right {
      width: calc(100% - 300px);
      padding: 30px 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .rations {
    padding: 30px 0 90px;
    &-filter__mob-btns {
      display: flex;
    }
    &__wrap {
      display: block;
    }
    &__content {
      width: 100%;
    }
    &__cards {
      display: none;
    }
    &__cards2 {
      display: grid;
    }
    &__title {
      font-size: 42px;
    }
    &__text {
      font-size: 16px;
    }
    &__sorting {
      display: none;
    }
  }
  .ration-card {
    .select__list {
      width: 300px;
    }
    &:nth-child(odd) {
      .select__list {
        left: 0;
        transform: translate(0, 0);
      }
    }
    &:nth-child(even) {
      .select__list {
        left: unset;
        right: 0;
        transform: translate(0, 0);
      }
    }
    &__favorite {
      width: 28px;
      height: 28px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(255, 255, 255, 0.30);
      backdrop-filter: blur(10px);
      border-radius: 4px;
      svg {
        width: 18px;
      }
    }
    @media screen and (min-width: 577px) {
      &__item:nth-child(1),
      &__item:nth-child(2) {
        .ration-card__item-hidden {
          left: 0;
          &:after {
            left: 0;
          }
        }
        .ration-card__item-wrap {
          border-radius: 20px 20px 20px 0;
        }
      }
      &__item:nth-last-child(-n+2) {
        .ration-card__item-hidden {
          left: unset;
          right: 0;
          &:after {
            left: unset;
            right: 0;
          }
        }
        .ration-card__item-wrap {
          border-radius: 20px 20px 0 20px;
        }
      }
    }
    &__item-hidden {
      left: -100px;
      width: 200px;
      &:after {
        left: 95px;
      }
    }
    &__item-wrap {
      padding: 10px;
    }
    &__item-title {
      font-size: 12px;
    }
    &__item-text {
      font-size: 10px;
    }
  }
  .rations-filter {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #F9F9F9;
    padding: 20px 20px 120px;
    margin-right: 0;
    overflow-x: hidden;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
    &__inner {
      gap: 0;
    }
    &__top {
      display: flex;
    }
    &__btn {
      display: none;
    }
    &__title {
      padding: 20px 0;
    }
    .title-show {
      cursor: pointer;
      background-image: url("../img/arrow2.svg");
      background-repeat: no-repeat;
      background-position: center right;
      background-size: 8px 15px;
      border-bottom: 1px solid rgba(161, 174, 186, 0.5);
    }
    &__wrap {
      position: fixed;
      z-index: 2;
      left: -105%;
      top: 0;
      width: 100%;
      height: 100%;
      background: #F9F9F9;
      padding: 20px 15px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      .hidden {
        display: block !important;
      }
    }
    &__group {
      .rations-filter__apply {
        display: flex;
      }
    }
    &__group-top {
      display: flex;
      margin-bottom: 35px;
    }
    &__group-mob {
      display: block;
    }
    &__range {
      margin-top: 20px;
    }
    &__flex {
      margin-top: 0;
    }
    &__apply {
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 576px) {
  .rations {
    &__title {
      font-size: 22px;
    }
    &__text {
      font-size: 14px;
      margin-top: 16px;
    }
    &-filter__mob-btn2 {
      flex-grow: 1;
    }
    &__cards {
      grid-template-columns: 100%;
    }
    &__btn {
      width: 280px;
      height: 60px;
    }
  }
  .ration-card {
    .select__list {
      left: 0 !important;
    }
    &__items {
      max-width: 140px;
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 45px / 4));
      gap: 15px;
      margin: 5px auto 0;
    }
    &__item {
      margin: 0 !important;
    }
  }
}

